module.exports = "/**\n * Colors used across the Navigator. Shared by the ATT&CK Website.\n * For each color-pair:\n *   - \"color\" refers to the color itself\n *   - \"on-color\" refers to the most readable text-color to appear on top of the color. \n * Note: some colors don't define on-colors. This is because, for these colors, they aren't intended to\n * appear with inner content. For example, \"link\" is only intended to be used for link text, which inherently\n * cannot have inner text.\n */\n.multiselect .stixType h1 {\n  font-size: 14px;\n  background: #f1f1f1;\n  padding: 5px;\n  margin: 0;\n}\n.multiselect .stixType .objects {\n  height: 150px;\n  overflow-y: scroll;\n}\n.multiselect .stixType .objects table {\n  border-collapse: collapse;\n  width: 100%;\n}\n.multiselect .stixType .objects table tr + tr {\n  border-top: 1px solid #f1f1f1;\n}\n.multiselect .stixType .objects table tr:hover {\n  background: #60c5ff;\n}\n.multiselect .stixType .objects table td {\n  width: 100px;\n  text-align: left;\n}\n.multiselect .stixType .objects table td + td {\n  width: 1px;\n}\n/*# sourceMappingURL=data:application/json;base64, */"